.App {
    text-align: center;
    font-family: "Roboto";
}

a {
    color: #007ba7;
}

tr.pointer {
    cursor: pointer;
}

.myModal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 70%;
    max-width: 1140px;
    height: 500px;
    /*margin-top: -100px;*/
    /*margin-left: -100px;*/
    border: 1px solid #ccc;
    background: white;
    overflow: auto;
    border-radius: 4px;
    outline: none;
    padding: 20px;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(1, 1, 1, 0.75);
}